import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AngularFireModule } from 'angularfire2';
import * as firebase from 'firebase';
import { AngularFireAuthModule } from 'angularfire2/auth';

// native
import { GoogleMaps } from '@ionic-native/google-maps';
import { Geolocation } from '@ionic-native/geolocation';
import { AuthProvider } from '../providers/auth/auth';

import { HttpClientModule } from '@angular/common/http';
import { WatsonConversationProvider } from '../providers/watson-conversation/watson-conversation';


const environment = {
  production: false,
  firebase: {
    apiKey: 'AIzaSyCLvN0wRAvVuYA3ZPY9FiAeT6AJDaxK2PI',
    authDomain: 'vc-web.firebaseapp.com',
    databaseURL: 'https://vc-web.firebaseio.com',
    projectId: 'vc-web',
    storageBucket: 'vc-web.appspot.com',
    messagingSenderId: '340692511205'
  }
};

@NgModule({
  declarations: [
    MyApp,
    ListPage
  ],
  imports: [
    HttpClientModule,
    AngularFireAuthModule,
    AngularFireModule.initializeApp(environment.firebase),
    BrowserModule,
    IonicModule.forRoot(MyApp, {
      platforms: {
        ios: {
          tabsPlacement: 'bottom',
        },
        android: {
          tabsPlacement: 'top'
        }
      }
    }, 
{
  links: [
    { loadChildren: '../pages/auth/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auth/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single/single.module.ngfactory#SinglePageModuleNgFactory', name: 'SinglePage', segment: 'single', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/test/test.module.ngfactory#TestPageModuleNgFactory', name: 'TestPage', segment: 'test', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ListPage
  ],
  providers: [
    Geolocation,
    GoogleMaps,
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthProvider,
    WatsonConversationProvider
  ]
})
export class AppModule {}



